import InfiniteAjaxScroll from "@webcreate/infinite-ajax-scroll";

function createsamuraiItem(samuraiData) {
  const template = `<div class="item">
      <div class="item__img"><img class="item__img__img" src="${samuraiData.meta.high_res_img_url}" onerror="this.src='https://placehold.co/150x150'" width="150" height="150"/></div>
      <h2 class="item__name">${samuraiData.meta.name}</h2>
      <div class="item__status">${samuraiData.meta.status}</div>
      <div class="item__rank">${samuraiData.meta.rank}</div>
  </div>
  `;

  let item = document.createElement("div");
  item.innerHTML = template.trim();

  return item.firstChild;
}

function nextHandler(pageIndex) {
  return fetch("./static/samurais.json")
    .then((response) => response.json())
    .then((data) => {
      let frag = document.createDocumentFragment();

      let itemsPerPage = 10;
      let totalPages = Math.ceil(data.length / itemsPerPage);
      let offset = pageIndex * itemsPerPage;

      // walk over the samurai items for the current page and add them to the fragment
      for (let i = offset, len = offset + itemsPerPage; i < len; i++) {
        let samurai = data[i];

        let item = createsamuraiItem(samurai);

        frag.appendChild(item);
      }

      let hasNextPage = pageIndex < totalPages - 1;

      return (
        this.append(Array.from(frag.childNodes))
          // indicate that there is a next page to load
          .then(() => hasNextPage)
      );
    });
}

window.ias = new InfiniteAjaxScroll(".container", {
  item: ".item",
  next: nextHandler,
  pagination: false
});
